
.log.search-form-params input[type="text"] {
    width: 10rem;
}
.log.search-form-params input[type="text"].date {
    width: 5rem;
    border-color: #999;
}
.log.search-form-params select {
    max-width: 15rem;
}
.result {
  margin: 2rem 0 1rem 0;
}
ul.log {
    margin: 1rem 0;
}
ul.log li {
  border-bottom: 1px solid #ccc;
}
ul.log li .user {
  margin-right: 2rem;
  color: midnightblue;
}
ul.log li .title {
  margin-right: 2rem;
  color: maroon;
}
ul.log li .date {
  float: right;
  margin-left: 2rem;
  padding-right: 0.5rem;
  font-size: 1rem;
  color: #333;
}
ul.log li .date .time {
  font-size: 0.9rem;
  color: #666;
}
ul.log li .avatar {
  float: left;
  margin-top: 0.1rem;
  padding-right: 1rem;
}
ul.log li .avatar img {
  width: 3rem;
  height: 3rem;
  border: 1px solid #ccc;
  border-radius: 50%;
}
ul.log li:last-child {
  border: none;
}
.page {
  border-bottom: 1px solid tan;
  margin-bottom: 0.25rem;
  font-size: 0.9rem;
  color: darkgoldenrod;
}



